    &.left-bottom {
      left: 20%;
      bottom: 25%;
      width: 20%;
      transform: rotate(315deg);
    }

    > img {
      width: 100%;
    }
  }
}

.article-block {
  margin-bottom: 2rem;

  > article {
    > h3 {
      font-size: 1.6rem;
      margin-top: 0;
      margin-bottom: 0.5rem;
    }

    > p {
      line-height: 2.5rem;
      margin-top: 0;
    }
  }
}

.article-block-with-image {
